


























import Vue from "vue";
export default Vue.extend({
  props: ["items", "title"],
  data: () => ({
    sheet: false,
  }),
  created() {
    this.$root.$refs.bottomSheet = this;
  },
  methods: {
    open() {
      this.sheet = true;
    },
    close() {
      this.sheet = false;
    },
  },
});
